@import url(https://fonts.googleapis.com/css2?family=Khula&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Khula&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    font-family: 'Khula', sans-serif;
    padding: 20px 0; 
    margin: 10px 10px 10px 10px;
}

.inner {
    display: block;
    white-space: nowrap;
}

.temperature {
    font-weight: 800;
    font-size: 48px;
    display: inline-block;
    padding: 10px 1% 0 0;
    vertical-align: top;
}
.weather-information {
    display: inline-block;
    padding: 0 1% 0 1%;
}

.city {
    display: inline-block;
    margin-left: 30%;
    vertical-align: top;
    font-size: 30px;
}

.weather-items {
    display: inline-block;
    vertical-align: top;
    font-size: 14px;
    line-height: 16px;
    color: #70757a;
    padding-left: 6px;
    padding-top: 13px;
}

ul {
    list-style-type: none;
    padding: 0;
}

.city-date-conditions {
      display: inline-block;
      line-height: 25px;
      vertical-align: top;
      text-align: right;
      padding-top: 10px;
}

.condition {
    margin-left: 195px;
    line-height: 15px;
}

.date {
    margin-left: 120px;
}

img {
    display: inline-block;
    vertical-align: top;
}

.weather-icon {
    height: 100px;
    width: 100px;
}


.Crypto {
    width: 250px;
    font-family: 'Khula';
    margin: 10px 10px 10px 10px;
    margin-top: 33px;
    margin-left: 50px;
}

.container {
    display: grid;
    grid-template-columns: 40% 5% 55%;
    grid-template-areas: 'change spacer price'
                         'change spacer time';
    grid-gap: 0px;
    gap: 0px;
    padding-top: 0;
}

.flex-inline {
    display: flex;
    flex-flow: row;
    padding-bottom: 0;
    margin: auto;
}

.hr-change {
    grid-area: change;
    font-size: 1.75rem;
    line-height: 0.6;
    padding-left: 2px;
}

.coin {
    font-size: 0.875rem;
    font-weight: bold;
    padding-left: 2px;
    margin-right: auto
}

.symbol {
    font-size: 20px;
    margin-left: 0;
}

.price {
    grid-area: price;
    font-size: 0.975rem;
    padding-bottom: 0px;
    margin-top: 3px;
    padding: 0;
    justify-self: end;
    font-weight: 400;
    line-height: 0.6;
}

.time {
    display: block;
    justify-self: end;
    grid-area: time;
    font-size: 0.875rem;
    color:grey;
}

.green {
    color: #18bb5c;
}

.red {
    color: #ff433d;
}
.container-f {
    display: flex;
    flex-flow: column;
}

.title-article {
    grid-area: title;
    font-size: 16px;
    font-weight: bolder;
}

.img-article {
    grid-area: image;
    border-radius: 3px;
    width: 176px;
    height: 99px;
    object-fit: cover;
}

.description-article {
    grid-area: description;
    line-height: 16px;
}

.source {
    font-size: 12px;
    font-weight: 300;
    color: #6e7780;
    grid-area: source;
}

.article-info {
    padding-left: 25px;
    font-size: 13px;
}

.article-container {
    display: grid;
    margin-bottom: 14px;
    width: 1000px;
    grid-template-areas: 'image source'
                         'image title'
                         'image description';
}
.parent {
    position: fixed; /* or absolute */
    top: 10%;
    left: 23%;
}

.flex-parent {
 display: grid;
 justify-content: space-around;
 grid-template-areas: 'header header header'
                      'news news news';
}

.crypto-main {
    grid-area: crypto;
    position: absolute;
}

.header-container {
    grid-area: header;
    display: flex;
    flex-flow: row nowrap;
}

.weather-main {
    grid-area: weather;
    margin-bottom: 500px;
}

.news-main {
    grid-area: news;
}
